@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
--primary-white: #ffffff;
--primary-light-gray: #f4f4f4;
--primary-gray: #ccd1d9;
--primary-dark-gray: #666666;
--primary-orange: #f9ab00;
--primary-dark:  rgba(52,58,64);
--primary-red: #ef4035;
--primary-hover-red: #f33501;
--primary-black: #000000;
--primary-transparent-black:rgba(0,0,0,0.7);
--primary-green: #03f137;
}

body{
  font-family: "Roboto Condensed", sans-serif !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo{
  width: 4rem;
}
/* .......................navbar............................. */

nav{
  z-index: 2;
}

nav a.nav-link{
  /* font-weight: 500; */
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff!important;
  color: var(--primary-white)!important;
  border-top: 0.1875rem solid rgba(52,58,64);
  border-top: 0.1875rem solid var(--primary-dark);  
}

/* .navbar-brand{
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
} */

.navbar-nav > li > a.active , .navbar-nav > li > a.active:focus {

  border-top: 0.1875rem solid #ffffff;

  border-top: 0.1875rem solid var(--primary-white);

}
.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid #ffffff;
  border-top: 0.1875rem solid var(--primary-white);
}

/* .......................header............................. */

.header-wraper{
  position: relative;
  background: url(/react-portfolio-project/static/media/wall-and-laptop-background.f9e274f4.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info{
  display: flex;
  position:absolute;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}
.main-info h1{
  color: #ffffff;
  color: var(--primary-white);
  text-transform: uppercase;
}



/* .......................Typed-text............................. */
.typed-text{
  font-size: 2rem;
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: none;

}

@media(max-width: 768px) {

  .main-info h1{
    font-size: 1.5rem !important;
  }
  .typed-text{
    font-size: 1.3rem !important;
  }
}
/* ......................header main offer............................. */

.btn-main-offer{
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem .75rem 0.625rem 0.75rem;
  color: #ffffff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover{
  text-decoration: none;
  background-color: #f9ab00;
  background-color: var(--primary-orange);
  transition: 0.2s ease-in-out;
}
/* ......................particle js............................. */
.tsparticles-canvas-el{
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width:768px){
  .particles-canvas{
    display: none;

  }
}

/* .......................about me............................. */


.photo-wrap{
  display: flex;
  justify-content: center;
   align-items: center;
}
.profile-img{
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.about-heading{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 35px;

}

@media(max-width: 468px){
  .profile-img{
    width: 10rem;

  }
}

/* .......................Services............................. */


.services{
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-gray);
}

.services h1{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}
.services .circle{
  position: relative;
  margin: 0.375rem auto;
  background: #f9ab00;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}
.services .icon{
  font-size: 1.4rem;
  color: #ffffff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.services .box{
  height: 16rem;
  border-bottom: 5px solid #f9ab00;
  border-bottom: 5px solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #ffffff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;

}
.services .box:hover{
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom: 5px solid rgba(52,58,64);
  border-bottom: 5px solid var(--primary-dark);
}

.services .box:hover .icon{
  color: rgba(52,58,64);
  color: var(--primary-dark);
}
.services .box:hover .circle{
  background: #ffffff;
  background: var(--primary-white);
}
.services .box:hover p{
  color: #ffffff;
  color: var(--primary-white);
}

/* .......................experience............................. */
.experience{
  padding: 0.7rem;

}


.experience-wrapper{
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;


}

.experience h1{
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);

}
.experience-wrapper::before{
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-gray);
  z-index: 1;

}

.timeline-block{
  width: calc(50%  + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;

}
.timeline-block-right{
  float: right;

}

.timeline-block-left{
  float: left;
  direction: rtl;
  
}
.marker{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgba(52,58,64);
  border: 0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content{
  width: 95%;
  padding: 0.09375rem;
  color: #666666;
  color: var(--primary-dark-gray);
  direction: ltr;
}
.timeline-content h3{
  margin-top: 0.3125rem;
  color: var(--prinary-red);
  font-weight: 900;
}
/* ..........media..................*/

@media(max-width: 768px){

.experience .container{
  padding-left: 0;
}

  .experience-wrapper::before{
    left: 0.5rem;
    margin-bottom: 1.875rem;


  }
  .timeline-block-right{
    float: none;
    width: 100%;
  }
  .timeline-block-left{
    float: none;
   direction: ltr;
   width: 100%;
  }
}
/* .......................portfolio............................. */

.portfolio-wrapper{
  background: #f4f4f4;
  background: var(--primary-light-gray);
  padding: 3rem 0;


}
.portfolio-wrapper h1{
  color: #f9ab00;
  color: var(--primary-orange);
}

.portfolio-image{
  width: 15rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
  
}

.portfolio-image-box{
  position: relative;
  margin: 1rem;
}

/* ......................overflow............................. */

.overflow{
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid var(--prinary-red);
  cursor: pointer;

}

.portfolio-image-box:hover .overflow{
  opacity: 60%;
  transition: 0.3s ease-in-out;

}
.portfolio-icon{
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon{
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

/* overflow box */

.portfolio-image-popupbox{
  width: 45rem;
  padding: 0.05rem;
}
.hyper-link{
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);
}
.hyper-link:hover{
  color: #f33501;
  color: var(--primary-hover-red);
  
}
/* media */

@media(max-width: 768px){
  .portfolio-image-popupbox{
    width: 100%;
  }
}

@media(max-height: 570px){

  .popupbox-wrapper{
    height: 100%;
  }
  .portfolio-image-popupbox{ 
    width: 50%;
  }

}

@media(max-height: 280px){

  .popupbox-wrapper{
    height: 90%;
  }
  .portfolio-image-popupbox{ 
    width: 30%;
  }

}

/* testimonials */

.testimonials{
  background-image: url(/react-portfolio-project/static/media/testimonials-bg.75612be7.jpg);
  background-size: cover;
  background-color: #000000;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}
.testimonials h1{
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 3rem 0;
  text-transform: uppercase;

}

.testimonials-content{
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background-color: rgba(0,0,0,0.7);
  background-color: var(--primary-transparent-black) ;
}
.myCarousel{
  color: #ffffff;
  color: var(--primary-white);
  background: transparent;
  margin-top: -6;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;

}

.myCarousel h3{
  color: #f9ab00;
  color: var(--primary-orange);
  font-weight: 600;
  text-transform: uppercase;

}

.myCarousel p{
  color: #ccd1d9;
  color: var(--primary-gray);
}

.carousel .slide img{
  width: 10rem !important;
  border-radius: 50%;
  
}
.carousel-root{
  margin: auto !important;
}

/* contact me  */

.contacts{
  background: rgba(0,0,0,0.7);
  background: var(--primary-transparent-black);
  padding: 3rem 0;

}

.contacts h1{
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;

}

.contacts p{
  color: #f4f4f4;
  color: var(--primary-light-gray);
  font-size: 1.2rem;
}

/* inputs */

.contacts input, .contacts textarea{
  width: 100%;
  box-sizing: border-box;
  background: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-gray);
  border-radius: 0;
  color: #f9ab00;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin-bottom: 1rem;
  padding-left: 0 !important;

}

.contacts .container{

  padding: 4rem 0;
  
}

.contacts input:focus, .contacts textarea:focus{
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
   border-bottom: 0.125rem solid #f9ab00;
   border-bottom: 0.125rem solid var(--primary-orange);
}

.contacts textarea{
  height: 12.2rem !important;

}
.contact-btn{
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;

}

.line{
  position: relative;
  width: 0;
  height: 0.125;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
  top: -0.8rem;
}
input:focus+.line, textarea:focus+.line{
  width: 100%;
  transition: width 0.8s ease-in-out;
}

input.form-control{
  margin: -0.3rem 0;
}
textarea.form-control{
  margin: -0.3rem 0;
}
.error-message{
  color: #ffffff;
  color: var(--primary-white);
  background: #f33501;
  background: var(--primary-hover-red);
  text-align: left;

}
.success-message{
  font-weight: 900;
  color: #f9ab00;
  color: var(--primary-orange);
}

/* media */

@media(max-width: 768px){

  .contacts{
    overflow: hidden;
  }
   .contacts .container{
     padding: 0 1rem;
   }

   .contacts p{
     padding: 0 0.4rem;
   }

}

/* footer components */

.footer{
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-gray);
  padding: 3rem;

}
.footer a{
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-gray);

}

.footer a:hover{
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange );
  cursor: pointer;

}

.footer .col{
  padding-right: 1rem;
  margin-bottom: 2rem;
}

.footer p{
  margin-bottom: 0;
}

